import { Box, Text } from "grommet";
import React, { useContext, useState } from "react";
import { NavLink, RouteComponentProps, withRouter } from "react-router-dom";
import styled from "styled-components";
import { UserContext } from "../../Context/Context";
import ReactTooltip from 'react-tooltip'
import NavAnchor from "../_Grommet/Text/NavAnchor ";

const Wrapper = styled(Box)`
	position: fixed;
	background-color: white;
	font-size: 28px;
	bottom: 0;
`;

const Links = styled(NavLink)`
	padding: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: 24px;
	color: #444444;
	transition all 1s;

	& i {
		width: 100%
	}
`;

const Anchor = styled(NavAnchor)`
	padding: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: 24px;
	color: #444444;
	transition all 1s;

	&:active {
		background-color: #BAD23B;
		color: white;
	}

	& i {
		width: 100%
	}
`;

const TopTip = styled(ReactTooltip)`
	z-index: 9999999;
`;

const Footer = ({ location }: RouteComponentProps) => {

	const [showSettingsMenu, setShowSettingsMenu] = useState(false);

	const { userState } = useContext(UserContext);

	return (
		<>
		<TopTip place="top" id="dashboard" effect="solid">Dashboard</TopTip>
		<ReactTooltip place="top" id="account" effect="solid" clickable={true}>Account</ReactTooltip>
		<ReactTooltip place="top" id="backup" effect="solid">Bet Backup</ReactTooltip>
		<ReactTooltip place="top" id="claims" effect="solid">Bet Claims</ReactTooltip>
		
		<TopTip place="top" id="settings" effect="solid">Settings</TopTip>
		
		
		<Wrapper
			// TODO: Fix dumb ZIndex Practices
			style={{zIndex: 999999}}
			elevation="medium"
			direction="row"
			width="100%"
			justify="center"
		>
			
			<Box flex="grow"
				style={{
					background: location.pathname === "/dashboard" ? "#BAD23B" : "initial",
				}}
			>
					<Anchor
						data-tip data-for='dashboard'
						path="/dashboard"
					>
						<Box>
							<i className="zwicon-home" 
								style={{
									color: location.pathname === "/dashboard" ? "#fff" : "initial"
								}}
							/>
						</Box>
					</Anchor>
			</Box>

			<Box flex="grow" style={{
						background: location.pathname === "/dashboard/wallet" ? "#BAD23B" : "initial",
					}}
			>
				<Anchor
					data-tip data-for='account'
					path="/dashboard/wallet"
				>
					<Box>
						<i className="zwicon-wallet" 
							style={{
								color: location.pathname === "/dashboard/wallet" ? "#fff" : "initial"
							}}
						/>
					</Box>
				</Anchor>
			</Box>
			
			<Box flex="grow" 
				style={{
					background: location.pathname === "/dashboard/bet-insurance" ? "#BAD23B" : "initial",
				}}
			>
				<Anchor
					data-tip data-for='backup'
					path="/dashboard/bet-insurance"
				>
					<Box>
						<i className="zwicon-piggy-bank" 
							style={{
								color: location.pathname === "/dashboard/bet-insurance" ? "#fff" : "initial"
							}}
						/>
					</Box>
				</Anchor>
			</Box>
			
			<Box flex="grow"
				style={{
					background: location.pathname === "/dashboard/claims" ? "#BAD23B" : "initial",
				}}
			>
				<Anchor
					data-tip data-for='claims'
					path="/dashboard/claims"
				>
					<Box>
						<i className="zwicon-money-bill" 
							style={{
								color: location.pathname === "/dashboard/claims" ? "#fff" : "initial"
							}}
						/>
					</Box>
				</Anchor>
			</Box>
			
			<Box flex="grow"
				style={{
					background: location.pathname === "/dashboard/settings" ? "#BAD23B" : "initial",
				}}
			>
				<Anchor
					data-tip data-for='settings'
					path="/dashboard/settings"
				>
					<Box>
						<i className="zwicon-cog" 
							style={{
								color: location.pathname === "/dashboard/settings" ? "#fff" : "initial"
							}}
						/>
					</Box>
				</Anchor>
			</Box>
		</Wrapper>
		</>
	);
};

export default withRouter(Footer);
